.menu__list {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: $font-heading;
  
  &--main {
    font-size: 1rem;
    line-height: 1.625rem;
  }
  
  &--no-margin {
    .menu__list {
      &__link {
        margin-right: 0;
      }
    }
  }
  
  &--login {
    .menu__list {
      &__link {
        letter-spacing: 1px;
        padding-right: $space-double;
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 7.5a3.749 3.749 0 003.75-3.75A3.749 3.749 0 007.5 0a3.749 3.749 0 00-3.75 3.75A3.749 3.749 0 007.5 7.5zm0 1.875c-2.503 0-7.5 1.256-7.5 3.75V15h15v-1.875c0-2.494-4.997-3.75-7.5-3.75z' fill='%23FD495C'/%3E%3C/svg%3E");
        background-position: right center;
        background-repeat: no-repeat;
        text-transform: uppercase;
      }
    }
  }
  
  &--logout {
    .menu__list {
      &__link {
        letter-spacing: 1px;
        padding-right: $space-double;
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 7.5a3.749 3.749 0 003.75-3.75A3.749 3.749 0 007.5 0a3.749 3.749 0 00-3.75 3.75A3.749 3.749 0 007.5 7.5zm0 1.875c-2.503 0-7.5 1.256-7.5 3.75V15h15v-1.875c0-2.494-4.997-3.75-7.5-3.75z' fill='%23FD495C'/%3E%3C/svg%3E");
        background-position: right center;
        background-repeat: no-repeat;
        text-transform: uppercase;
      }
    }
  }
  
  &__link {
    text-decoration: none;
    margin-right: $space-one-and-a-half;
    color: $navy;
    
    &:hover,
    &--active {
      text-decoration: underline;
    }
  }
}
