.floater {
  position: relative;
  z-index: 2;
  transform-style: preserve-3d;
  
  img {
    display: block;
  }
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 50%;
    margin-top: -1.5rem;
    transform: translateZ(-1px);
    background-color: $true-blue;
  }
  
  &--left {
    &::after {
      left: 0;
      right: 50%;
      margin-left: -1.3rem;
    }
  }
  
  &--right {
    &::after {
      left: 50%;
      right: 0;
      margin-right: -1.3rem;
    }
    
    &--small {
      &::after {
        left: 66.66%;
        bottom: 66.66%;
        right: 0;
        margin-right: -1.1rem;
      }
    }
  }
  
  &--coral {
    &::after {
      background-color: $coral;
    }
  }
  
  &--azure {
    &::after {
      background-color: $azure;
    }
  }
}
