.cta {
  background-color: $light-horizons;
  padding: $space-sextuple;
  text-align: center;
  
  &__heading {
    font-size: 3.375rem;
    line-height: 1.22222222222;
    margin: 0 0 $space * 1.5;
    color: $navy;
  }
  
  &__body {
    font-size: 1.25rem;
    line-height: 1.3;
    margin: 0 auto $space-double;
    font-family: $font-heading;
    font-weight: 300;
    color: $navy;
    max-width: 60ch;
  }
  
  &__link {
    @include link;
    
    &--button {
      @include button-base;
      @include button-medium;
      
      &--engagement {
        @include button-color-secondary;
      }
    }
  }
}
