.text {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  background-repeat: no-repeat;
  background-size: cover;
  height: 480px;
  max-height: 480px;
  
  &__title {
    font-size: 3rem;
    margin: 0 0 1rem;
    line-height: 1.20833333333;
    font-weight: 300;
    color: $navy;
  }
  
  &__intro {
    font-size: 1.75rem;
    font-family: $font-sans;
    font-weight: 300;
    line-height: 1.2;
    color: $navy;
  }
  
  &__wrap {
    @include wrapper;
    
    display: flex;
    height: 100%;
  }
  
  &__text {
    max-width: 53.125rem;
    margin: 0 auto;
    font-size: 1.25rem;
    line-height: 1.3;
    color: $navy;
    font-family: $font-sans;
    font-weight: 300;
  }
}
