// Sass map to define colors. Each set should have a "Dark" variation when required.
// e.g. `default` and `default-dark`. Or `purple` and `purple-dark`.
$button-colors: (
  primary: (
    text: clr(text-inverse),
    bg: clr(btn-primary-bg),
    text-hover: clr(text-inverse),
    bg-hover: clr(btn-primary-hover),
  ),
  primary-dark: (
    text: clr(text),
    bg: clr(btn-primary-bg),
    text-hover: clr(text),
    bg-hover: clr(btn-primary-hover),
  ),
  secondary: (
    text: clr(text-inverse),
    bg: clr(btn-engagement-bg),
    text-hover: clr(text-inverse),
    bg-hover: clr(btn-engagement-hover),
  ),
  secondary-dark: (
    text: clr(text),
    bg: clr(btn-engagement-bg),
    text-hover: clr(text),
    bg-hover: clr(btn-engagement-hover),
  ),
);

// Mixin to define colors for one or more schemes.
// Simply pass the color scheme to the mixin to get the colors defined in the map.
// e.g. @include buttonColors(default) will get all of the colors defined in the "default" section.
@mixin buttonColors(
  $scheme,
  $pallate: map-get($button-colors, $scheme),
  $map: $button-colors
) {
  color: map-get($pallate, text);
  background-color: map-get($pallate, bg);

  &:visited {
    color: map-get($pallate, text);
  }

  &:hover {
    color: map-get($pallate, text-hover);
    background-color: map-get($pallate, bg-hover);
  }
}

// The button-base mixin contains styles that apply to all buttons
// regardless of color or size.
@mixin button-base {
  border: none;
  border-radius: 100px;
  cursor: pointer;
  display: inline-block;
  font-family: $font-button;
  letter-spacing: 1px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
}

// Button color variations
// Note how we include dark mode options for each
@mixin button-color-primary {
  @include buttonColors(primary);

  @media (prefers-color-scheme: dark) {
    @include buttonColors(primary-dark);
  }
}

@mixin button-color-secondary {
  @include buttonColors(secondary);

  @media (prefers-color-scheme: dark) {
    @include buttonColors(secondary-dark);
  }
}

// Button size variations
@mixin button-medium {
  font-size: 0.875rem;
  line-height: 1.6;
  padding: 0.4rem $space-one-and-a-half;
}

@mixin button-large {
  font-size: 1rem;
  line-height: 1.5;
  padding: 0.70rem $space-one-and-a-half calc(0.70rem + 2px);
}

.button {
  @include button-base;
  
  &--primary {
    @include button-color-primary;
  }
  
  &--large {
    @include button-large;
  }
  
  &--small {
    @include button-medium;
  }

  &--engagement {
    @include button-color-secondary;
  }
  
  &--external {
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='20' viewBox='0 0 12 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.2508 4.26632C7.2508 3.96849 7.48521 3.72705 7.77436 3.72705L12 3.72705V8.07953C12 8.37736 11.7656 8.61879 11.4764 8.61879C11.1873 8.61879 10.9529 8.37736 10.9529 8.07953V5.56823L5.18289 11.5114C4.97843 11.722 4.64694 11.722 4.44248 11.5114C4.23802 11.3008 4.23802 10.9594 4.44248 10.7488L10.2125 4.80559L7.77436 4.80559C7.48521 4.80559 7.2508 4.56415 7.2508 4.26632Z' fill='white'/%3E%3Cpath d='M8.22887 7.2512H7.8379L8.74766 6.31413C9.06327 6.49991 9.27597 6.84997 9.27597 7.2512V7.29525L8.22887 8.37378V7.2512Z' fill='white'/%3E%3Cpath d='M8.22887 9.89907V14.6485H1.04711L1.04711 7.2512L6.35707 7.2512L7.40418 6.17266L1.04711 6.17266C0.468807 6.17266 1.15847e-07 6.65554 0 7.2512L2.20676e-08 14.6485C5.51799e-09 15.2442 0.468808 15.7271 1.04711 15.7271H8.22887C8.80717 15.7271 9.27597 15.2442 9.27597 14.6485V8.82053L8.22887 9.89907Z' fill='white'/%3E%3C/svg%3E%0A");
    background-position: right $space center;
    padding-right: $space-two-and-a-half;
  }
}
