$color-breadcrumb: clr(accent);
$color-breadcrumb-hover: clr(accent-high);
$color-breadcrumb-active: clr(text);

.breadcrumb {
  @include list-reset;
}

.breadcrumb__item,
.breadcrumb__link,
.breadcrumb__link:link,
.breadcrumb__link:visited,
.breadcrumb__link:focus {
  color: $color-breadcrumb;
  text-decoration: none;
}

.breadcrumb__link:hover {
  color: $color-breadcrumb-hover;
}

.breadcrumb__link--home {
  position: relative;
  bottom: -3px;
}

.breadcrumb__item {
  display: inline-block;
  font-size: 1.125rem;
  margin: 0 auto $space-double 0.25rem;
  font-family: $font-heading;
  font-weight: 300;
  color: $navy;
  max-width: 60ch;

  &::after {
    content: '';
    background-image: url("data:image/svg+xml,%3Csvg width='7' height='10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M.98 1C1.025.969.932.969.98 1l6 4c.046.031 0-.062 0 0s.046-.032 0 0l-6 4c-.024.015.026 0 0 0s.023.015 0 0c-.047-.031 0 .062 0 0V1c-.047.03 0-.062 0 0z' fill='%23009ADE'/%3E%3C/svg%3E");
    height: 0.5rem;
    width: 0.375rem;
    background-size: contain;
    display: inline-block;
    margin-left: 0.25rem;
    position: relative;
    top: -2px;
  }

  &:last-child {
    color: $color-breadcrumb-active;

    &::after {
      content: '';
      background: none;
    }
  }
}

.breadcrumb_link:active {
  color: $color-breadcrumb-active;
}
