.card {
  background-color: $light-horizons;
  
  &__content {
    padding: $space-double;
    padding-top: $space-two-and-a-half;
    padding-bottom: $space-two-and-a-half;
  }
  
  &__heading {
    font-size: 2.125rem;
    line-height: 1.23529411765;
    margin: 0 0 $space * .75;
    font-family: $font-serif;
    font-weight: 300;
    color: $navy;
    
    a {
      color: $navy;
      text-decoration: none;
    }
  }
  
  &__body {
    font-size: 1.25rem;
    line-height: 1.3;
    margin: 0 0 $space-double;
    font-family: $font-heading;
    font-weight: 300;
    color: $navy;
  }
  
  &__link {
    @include link;
    
    &--button {
      @include button-base;
      @include button-medium;
      
      &--primary {
        @include button-color-primary;
      }
    }
  }
}
