.twi {
  background: #fff;
  
  &__wrap {
    display: flex;
    align-items: center;
    margin: 0 auto;
    max-width: 1100px;
  }
  
  &__heading {
    font-size: 2.125rem;
    line-height: 1.23529411765;
    margin: 0 0 $space * .75;
    font-family: $font-serif;
    font-weight: 300;
    color: $navy;
  }
  
  &__body {
    font-size: 1.25rem;
    line-height: 1.3;
    margin: 0 auto $space-double;
    font-family: $font-heading;
    font-weight: 300;
    color: $navy;
    max-width: 60ch;
  }
  
  &__text {
    padding-right: $space-quintuple;
    max-width: 550px;
  }
  
  &__image {
    background: #fff;
  }
  
  &__link {
    @include link;
    
    &--button {
      @include button-base;
      @include button-medium;
      
      &--primary {
        @include button-color-primary;
      }
    }
  }
}
