.hero {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  background-repeat: no-repeat;
  background-size: cover;
  height: 480px;
  max-height: 480px;
  
  &__wrap {
    @include wrapper;
    
    display: flex;
    height: 100%;
  }
  
  &__text {
    flex: 0.5;
    display: flex;
    align-items: center;
    padding: $space-triple;
    position: relative;
    
    h1 {
      margin: 0;
      font-size: 3.375rem;
      line-height: 1.22222222222;
      color: #fff;
      position: relative;
      z-index: 2;
    }
    
    &__bg {
      background: #1C355E;
      mix-blend-mode: multiply;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }
}
